.team {
    /* display: flex; */
}

.team__flow {
    height: calc(100vh - 100px);
    /* height: 100vh; */
    width: 100vw;




}


  
  .floatingedges .react-flow__handle {
    opacity: 0;
  }



@media screen and (max-width: 768px) {
    .team__flow {
        height: calc(100vh - 194px)
    }
}

.team__sidebar {
    display: flex;
    flex-direction: column;
    /* align-items: flex-start; */
    background-color: rgba(255, 255, 255, 1);
}

.team__sidebar_title_container {
    display: flex;
    margin: 0;
    padding: 32px 24px 40px 24px;
    width: 330px;
    /* justify-content: space-between; */
}

.team__sidebar_title {
    font-family: 'Roboto', 'Arial', sans-serif;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    margin: 0;
    padding: 0;


}

.team__sidebar_button_close {
    background-color: transparent;
    border: none;
    cursor: pointer;
    margin: 0 0 0 40px;
    padding: 0;
}